.story__background {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 0;
  left: 0;
  bottom: 0;
  background-color: $c-dark;

  transition: height 0.5s ease-out;

  &.active {
    height: 100%;
  }
}
